 */

fieldset {
  border: 0;
  margin: 0;
  padding: 0;
}

/* ==========================================================================
   Typo
   ========================================================================== */

html {
  background-color: #f4f7f9;
}

.logo-section {
  padding: 4em 0;
  /*background-color: #2b414d;*/
}

.logo {
  max-width: 800px;
  margin: 0 auto;

  text-align: center;
  /* text on yellow*/
  /*color: #fffce1;*/
  color: #fff;
}

.square {
  position: relative;
  width: 300px;
  height: 300px;
  margin: 0 auto;

  /* yellow */
  background: #efc75e;
}

.logo h1 {
  position: absolute;
  bottom: 0;
  right: 0;
  margin: .2em .5em;
}

.logo h2 {
  margin-top: .5em;
  font-weight: 400;
  color: rgba(255, 255, 255, .5);
  color: rgba(0, 0, 0, 0.6);
}

/* ==========================================================================
   Sections
   ========================================================================== */

section {
  padding: 0 0 2em 0;
}

.section-content {
  max-width: 800px;
  margin: 0 auto;
}

.section-dark {
  background-color: #2b414d;
}

/* ==========================================================================
   Example Blocks
   ========================================================================== */

.example-title {
  font-size: 2rem;
  color: rgba(0, 0, 0, 0.9);
}

.example-sheet {
  padding: 1em 2em;

  font-size: 1.5rem;
  border: 1px solid #eee;
  background-color: #fff;
}

.code-example {
  margin: 1rem 0;
  font-size: 1.3rem;
}

.code-example pre {
  border: 1px solid #eee;
}

.code-example h3 {
  font-size: 1rem;
  color: rgba(0, 0, 0, 0.75);
  margin: 0;
}

/* old stuff */

article {
  max-width: 660px;
  margin: 0 auto;
}

[contenteditable="true"]:focus {
  background-color: #eee;
}

.iframe-container iframe{
  width: 100%;
  height: 500px;
}


