   ========================================================================== */


.highlight {
  background: #ecbc3f;
  color: #fff;
  border-radius: 10px;
  padding: 1px 5px;
}

.highlight-spellcheck {
  border-bottom: 1px solid #e55e4e;
  background-color: #fdefee;
}

.highlight-whitespace {
  background: rgba(7, 165, 206, .1);
  border-bottom: 1px solid rgba(7, 165, 206, .5);
}

.highlight-comment {
  background-color: #f8c13075;
}


/* ==========================================================================
   Events List
   ========================================================================== */

.events-list {
  margin: 0;
  padding: 0;
}

.events-list .events-list-entry {
  list-style: none;
  margin-bottom: 2px;
  opacity: .6;
  transition: opacity 1s ease-in;
}

.events-list .events-list-entry:last-child {
  margin-bottom: 0;
}


/* Event List Animations */

.events-list .events-enter {
  opacity: 1;
}

.events-list .events-enter.events-enter-active {
  opacity: .99;
}


/* Event styles */

.event-name {
  display: inline-block;
  padding: 0 10px;
  margin-right: 5px;

  border-radius: 5px;
  font-size: 1rem;
  background: #eab730;
  color: #fff;
  font-weight: 400;
}

.selection {
  display: inline-block;
  background: #b3d4fc;
}

.cursor-position {
  color: #49a345;
}

.cursor-position i {
  display: inline-block;
  width: 1px;
  height: 100%;
  position: relative;
  top: -3px;
  border-right: 2px solid #0c86b1;
  margin-right: 1px;
}

.clipboard-action {
  color: #666;
}

.clipboard-content {
  color: #49a345;
}

/* ==========================================================================
   Styling
   ========================================================================== */

.example-style-default {
}
.example-style-default:focus {
}

.example-style-dark {
  padding: 0.5em;
  color: white;
  background-color: darkgrey;
  border: 1px solid darkgrey;
  border-radius: 4px;
  caret-color: yellow;
}
.example-style-dark:focus {
  outline: none;
  background-color: dimgrey;
  box-shadow: 0px 0px 8px black;
}

/* ==========================================================================
   iFrame
   ========================================================================== */

.iframe-sheet {
  padding: 1em;
  border: 1px solid #eee;
  background-color: #fff;
}

.iframe-example {
  width: 100%;
  min-height: 500px;

  border: 1px solid #ccc;
  border-top: 1px solid #666;
  border-left: 1px solid #666;
  box-sizing: border-box;
}

/* ==========================================================================
   Documentation
   ========================================================================== */

.documentation {
  border-bottom: 1px solid rgba(0, 0, 0, .5);
}

.documentation h4 {
  font-size: 1.2rem;
}

.documentation h4 span {
  display: inline-block;
  padding: 3px 10px 2px;
  border-radius: 5px;
  background: #eab730;
  color: #fff;
  font-weight: 400;
}

.documentation pre {
  font-size: 1rem;
}

.edit-example {
  padding: 1em 2em;

  font-size: 1.5rem;
  border: 1px solid #eee;
  background-color: #fff;
}

.cursor {
  display: inline-block;
  width: 1px;
  height: 100%;
  position: relative;
  top: -3px;
  border-right: 2px solid #0c86b1;
  margin-right: 1px;
}


/* ==========================================================================
   Helpers
   ========================================================================== */

.space-after {
  margin-bottom: 5em;
}

/*
 * Clearfix: contain floats
 *
 * For modern browsers
 * 1. The space content is one way to avoid an Opera bug when the
 *    \`contenteditable\` attribute is included anywhere else in the document.
 *    Otherwise it causes space to appear at the top and bottom of elements
 *    that receive the \`clearfix\` class.
